import React from 'react';

const Recipe=(props)=>{
    return(
    <>
    <div className="box">
        <h1>{props.title}</h1>
        <p>{props.calories}</p>
        <img src={props.image} alt=""></img>
        
        <ul>
            {
                props.ingredients.map(ingredient=>(
                    <li>{ingredient.text}</li>
                ))
            }
        </ul>
        <button className="button1"><a href={props.link} target="_blank">Know More</a></button>
    </div>
    </>)
}




export default Recipe;
