import React, { useEffect, useState } from "react";
import Recipe from "./Recipe";
import "./index.css"


const App = () => {
    const Api_Id="f00c0052";
    const Api_Key="89c6a411596d755157abaf9bd4585ad0";
    
    
    const [recipes,setRecipes]=useState([]);
    const [search, setSearch]=useState("");
    const [query,setQuery]=useState("pie");

    const req=`https://api.edamam.com/search?q=${query}&app_id=${Api_Id}&app_key=${Api_Key}`;

    useEffect(()=>{
        getreq();
    },[query])

    const getreq = async()=>{
        const reqs= await fetch(req);
        const data= await reqs.json();
        setRecipes(data.hits);
        console.log(data)
    }
    
    const updatevale= e  =>{
        setSearch(e.target.value);
    }

    const getseach=e=>{
        e.preventDefault();
        setQuery(search);
    }

    return(
        <>
            <div>
            <div className="head">
                <h1>Search Your Favourite Recipe</h1>
            </div>
                <form onSubmit={getseach} className="search">
                    <input value={search} className="input" type='text' onChange={updatevale}></input>
                    <button className="button">Search</button>
                </form>
            </div>
            <div className="bg_color">
            <h2>Showing Search result for {query}</h2>
            <div className="recipe">
            
            {
                recipes.map(recipe => (
                    
                <Recipe 
                    key={recipe,recipe.label}
                    title={recipe.recipe.label}
                    calories={recipe.recipe.calories}
                    image={recipe.recipe.image}
                    ingredients={recipe.recipe.ingredients}
                    link={recipe.recipe.url}
                />
                ))
            }
            </div>
            </div>
        </>
    )
}
    
    


export default App;